body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  text-align: center;
  color: white;
  background-image: url(/weather-app/static/media/city_vector.3f46e2d8.jpg);
}
.btn-color {
  color: white;
  border: 1px solid white;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  color: white;
}
.resize {
  align-self: stretch;
}
span {
  display: block;
}
.navbar-color {
  background-color: rgba(31, 34, 118, 0.5);
  color: white;
}
.brand-color {
  color: white;
}
.imgSize {
  height: 40px;
  width: 40px;
}

.Weather-App {
  position: absolute;
  min-height: 100vh;
  min-width: 100vw;
  top: 0;
  bottom: 0;

  background-color: rgba(31, 34, 118, 0.5);
  z-index: -5;
}
.weather {
  border: 1px solid white;
  border-radius: 5px;
  margin-top: 10rem;
  background-color: rgba(124, 138, 216, 0.5);
}
.desc {
  font-size: 3rem;
}
#temp {
  font-size: 6rem;
}

